
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  color: #444444;

  /* text-align:justify; */
}
.page-banner{
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  z-index: 3;
}
.page-title{
  color: #cccccc;
  z-index: 2;
  position: absolute;
  top: 40px;
  right: 40px;
  font-size: 28px;
  font-weight: bolder;
  transition: all 0.5s;
}
.router-wrapper{
  margin: 80px 0 50px 0;
  position: absolute;
  top: 0px;
  width: calc(100vw);
  height: calc(100vh - 130px);
  overflow: auto;
  
  transition: all 0.5s;
}
.router-wrapper::-webkit-scrollbar{
  width: 0.5em;
  background-color: white;
}
.router-wrapper::-webkit-scrollbar-thumb{
  background-color: #cccccc;
}


body{
  /* background: beige; */
  margin: 0;
  /* height: 100%; */
}
.tween-container{
  position: relative;
  /* background: white; */

  width: 100%;
  height: 100vh;
  transition-delay: 0.4s;

  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;

  overflow: hidden;
}
.tween-button{
  background: blueviolet;
  position: absolute;
  transition-property: width, height, border-radius, transform, border;
  transition-duration: 0.4s, 0.4s, 0.1s, 0.4s, 0s;
  transition-delay: 0s, 0s, 0.3s, 0s, 0.4s;
  pointer-events: auto;
}
.tween-slot{
  position: relative;
  pointer-events: none;
}

















.fab{
  width: 56px;
  height: 56px;
  background: blueviolet;
  border-radius: 50%;
  position: absolute;
  
  display: flex;
  flex-flow:row nowrap;
  justify-content: center;
  align-items: center;

  color: white;
}
.fab-icon{
  position: absolute;
  height: 18px;
  width: 18px;

  display: flex;
  flex-flow:row nowrap;
  justify-content: center;
  align-items: center;
}
.tooltip{
  position: absolute;
  transition:opacity 0.2s, transform 0.4s;
  transition-delay: 0.2s, 0s;
  min-width: 300px;
}


.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  transform: scale(-1, 1);
}
.fa-flip-vertical {
  transform: scale(1, -1);
}
.fa-spin {
  animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  animation: fa-spin 1s infinite steps(8);
}
@keyframes fa-spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}

