.resume{
  display: flex;
  flex-flow: row nowrap;
  justify-content:flex-start;
  align-items:flex-start;
  opacity: 0;
  transition: opacity 0.5s;
}
.resume-col:first-of-type{
  min-width: 18em;
  width: 50em;
  display: flex;
  flex-flow: row wrap;
  overflow: auto;
  padding: 0 20px 0 40px;
  background: #e26181;
  color: white;
}
.resume-col:first-of-type::-webkit-scrollbar{
  width: 0.5em;
  background-color:#e58ea3;
}
.resume-col:first-of-type::-webkit-scrollbar-thumb{
  background-color:#f2c9d2;
}
.resume-col::-webkit-scrollbar{
  width: 0.5em;
  background-color: white;
}
.resume-col::-webkit-scrollbar-thumb{
  background-color: #cccccc;
}
.skill-item{
  display: inline-block;
}
.resume-col{
  height: calc(100vh - 110px);
  overflow: auto;
  padding: 0 40px 0 30px;
  /* width: calc(100px - 20em); */
  transition: all 0.5s;
}
h1{
  font-size: 1.2em;
  margin-bottom: 0;
  margin-top:0.5em;
  width: 100%;
}
.h1-left{
  color: #f2c9d2;
}
.h1-right{
  color: #aaaaaa;
}
h2{
  font-size: 1.5em;
  margin-bottom: 0;
  margin-top:0.2em;
}
.h2-small{
  font-size: 1.2em;
}
.font-dark{
  color:#446594;
}
.em{
  font-weight: bold;
  font-style: italic;
}
.tag{
  border-bottom: solid 1px #f2c9d2;
  color: #f2c9d2;
  display: inline-block;
  
  margin: 0.2em;
}
.tag-dark{
  border: solid 1px #e58ea3;
  color:#e58ea3;
  padding: 0 0.2em;
}
.exp{
  width: 100%;
}
.project{
  width: 20em;
  padding: 1em;
  margin: 1em 1em 1em 0;
  border: solid 1px #dddddd;
  box-shadow: 2px 4px 5px #eeeeee;
}
.bullet::before{
  content: '\2022   ';
}
.bullet{
  display: block;
}
@media (max-width: 1280px){
.project{
    display:block;
    width: 100%;
}
.projects{
    width: 100%;
    display:flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    margin: 0 1em;
}
}
@media (max-width: 870px){
.resume-col{
    width: 100%;
    padding: 0 40px 0 40px;
}
.resume{
    flex-flow: row wrap;
}
}
@media (min-width: 1280px) {
.projects{
    width:100%;
    columns: 2;
    column-gap: 0;
}
.project{
    display: inline-block;
}
}
a:focus, a:visited,a:active{
  color:#446594;
}
a:hover{
  color:#e26181
}













































